import React from "react";
import style from './Users.module.css';
import User from "./User/User";
import default_logo from "./../assets/user-default-logo.png"
import ShowLoadingAnimation from "../common/ShowLoadingAnimation/ShowLoadingAnimation";
import Paginator from "./../common/Paginator/Paginator";

const Users = React.memo((props) => {
    let friendItem = props.users.map(u =>
        <User key={u.id}
            img={(u.photos.small) ? u.photos.small : default_logo}
            fullname={u.name}
            status={(u.status) ? u.status : 'Test'}
            // country={u.location.country}
            // city={u.location.city}
            isFollowed={u.followed}
            id={u.id}
            isFollowClicked={props.isFollowClicked}
            followUser={props.followUser}
            unfollowUser={props.unfollowUser}
        />
    )
    return <div className={style.friendsContent}>
        {(props.isLoadingStatus) ? <ShowLoadingAnimation/> : ''}
                <Paginator totalPage={props.totalPage}
                           perPage={props.perPage}
                           currentPage={props.currentPage}
                           onPageChanged={props.onPageChanged}
                           pageInterval={20}
                />
        {friendItem}
        <button onClick={props.onMoreButtonClick} className={style.buttonReadMore}>Show More..</button>

    </div>
})

export default Users;

